* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  overflow-y: scroll;
}

body {
  font-size: 14px;
  font-family: "ProximaNova-Regular", serif, sans-serif;
  color: #1e1e1e;
  background: #f4f7fc;
  scrollbar-gutter: "stable both-edges";
}

img {
  vertical-align: top !important;
  display: inline-block !important;
  max-width: 100%;
}

h2 {
  font-weight: 600;
  font-size: 22px;
  line-height: 27px;
}

h3 {
  font-weight: 600;
  font-size: 22px;
  line-height: 33px;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 15px;
}

.text-center {
  text-align: center;
}

.img-fluid {
  max-width: 100%;
  height: auto;
}

.text-uppercase {
  text-transform: uppercase;
}
.flex-start {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}
.flex-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

@font-face {
  font-family: "ProximaNova-Regular";
  src: url("./assets/fonts/ProximaNova-Regular.eot");
  src: url("./assets/fonts/ProximaNova-Regular.eot?#iefix")
      format("embedded-opentype"),
    url("./assets/fonts/ProximaNova-Regular.svg#ProximaNova-Regular")
      format("svg"),
    url("./assets/fonts/ProximaNova-Regular.ttf") format("truetype"),
    url("./assets/fonts/ProximaNova-Regular.woff") format("woff"),
    url("./assets/fonts/ProximaNova-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Proxima Nova Semibold";
  src: url("./assets/fonts/Proxima Nova Semibold.eot");
  src: url("./assets/fonts/Proxima Nova Semibold.eot?#iefix")
      format("embedded-opentype"),
    url("./assets/fonts/Proxima Nova Semibold.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
}

