::-webkit-input-placeholder {
  /* Edge */
  color: rgba(30, 30, 30, 0.5) !important;
  opacity: 1 !important;
}

:-ms-input-placeholder {
  /* Internet Explorer */
  color: rgba(30, 30, 30, 0.5) !important;
  opacity: 1 !important;
}

::placeholder {
  color: rgba(30, 30, 30, 0.5) !important;
  opacity: 1 !important;
}

.primary-button {
  background: #f7a823;
  color: #fff;
  padding: 12px 24px;
  font-size: 14px;
  line-height: 14px;
  height: 40px;
  box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.05);
  border-radius: 3px;
  text-transform: capitalize;
  font-family: "Proxima Nova Semibold", serif, sans-serif;
  border: 1px solid #f7a823;
  cursor: pointer;
}

.primary-button:hover {
  background: #d98c08;
  color: #f4f7fc;
}
.secondary-button {
  background: transparent;
  color: #f7a823;
  padding: 12px 24px;
  font-size: 14px;
  line-height: 14px;
  height: 40px;
  /* box-shadow: 0px 2px 3px rgb(0 0 0 / 5%); */
  border-radius: 3px;
  text-transform: capitalize;
  font-family: "Proxima Nova Semibold", serif, sans-serif;
  border: 1px solid #f7a823;
  cursor: pointer;
}
.secondary-button:hover {
  background: rgba(247, 168, 35, 0.1);
  color: rgba(217, 140, 8, 1);
}
