.form-group > select {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  display: block;
  background-color: transparent;
  outline: 0;
  width: 100%;
  -webkit-box-shadow: none;
  box-shadow: none;
  border-radius: 3px;
  line-height: inherit;
  background-image: none;
  padding: 0 15px;
  height: 45px;
  border: 1px solid rgba(30, 30, 30, 0.15);
  font-size: 14px;
  color: #1e1e1e80;
}

.select-field select {
  width: 100%;
  padding: 0 15px;
